.navbar {
  color: #fff;
  display: flex;
  padding: 1rem 0;
  place-content: center;
  position: absolute;
  width: 100%;
  background-color: transparent;
  z-index: 1;
}

.navbar-nav {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-item {
  width: 80px;
}

.nav-link {
  text-decoration: none;
  color: #ffffff;
  padding: 0.5rem;
}

@media (hover: hover) and (pointer: fine) {
  .nav-link:hover {
    background-color: #303030;
  }
}
