.album-link-list {
    padding: 0 40px;
    background-color: black;
}

.album-link-list a {
    display: inline-block;
    height: 0;
}

.fa-large {
    font-size: 50px;
    width: 50px;
    margin: 15px;
    color: white;
}

.tidal-icon {
    margin: 10px;
    font-size: 50px;
    width: 50px;
    fill: white;
}

.iheartradio-icon {
    margin: 10px 15px;
    width: 50px;
    fill: white;
    height: 45px;
    padding-top: 10px;
}

.iheartradio-icon path {
    transform: scale(150%);
}

.pandora-icon {
    margin: 10px 15px;
    width: 50px;
    fill: white;
    border-radius: 50%;
}

.pandora-icon path {
    transform: scale(157%);
}

.applemusic-icon {
    margin: 10px;
    width: 50px;
}

.applemusic-icon rect {
    fill: white;
}

.applemusic-icon path {
    transform: scale(0.1);
    fill: #122043;
}

.audiomack-icon {
    width: 50px;
    fill: white;
    margin: 0px 15px;
}

.audiomack-icon path {
    transform: scale(150%);
}

@media (hover: hover) and (pointer: fine) {
    .fa-large:hover {
        opacity: 0.7;
    }

    .tidal-icon:hover {
        opacity: 0.7;
    }

    .iheartradio-icon:hover {
        opacity: 0.7;
    }

    .pandora-icon:hover {
        opacity: 0.7;
    }

    .applemusic-icon:hover {
        opacity: 0.7;
    }

    .audiomack-icon:hover {
        opacity: 0.7;
    }
}